.w-18 {
  width: 18%;
}
.nav-small {
  display: block;
  margin: 1rem auto;
  text-align: center;
  background-color: #d3d3ab;
}
.nav-small li {
  display: inline-block;
  padding: 0 4px;
}
@media (min-width: 768px) {
  .nav-small {
    display: none;
  }
}

.nav-large {
  display: none;
}
@media (min-width: 768px) {
  .nav-large {
    display: block;
  }
}
.m2m-current {
  color: #818172;
}

.m2m-right-arrow::after {
  display: inline-block;
  content: "";
  padding-left: 2px;
}

.m2m-left-arrow::before {
  display: inline-block;
  content: "";
  padding-right: 2px;
}

p {
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}
p.lead {
  font-size: calc(1.2875rem + 0.45vw);
  line-height: 1.3 !important;
  text-indent: 0;
  text-align: left !important;
  margin: 2rem 0 !important;
}

code {
  background-color: #fff;
  padding: 0.1rem;
  border-radius: 3px;
}

.m2m-font-micro {
  font-size: 0.7875em;
  line-height: 1.2;
}

blockquote {
  margin-top: 2rem;
  padding-left: 1.25rem;
  border-left: 2px solid #d3d3ab;
  line-height: 1.3;
}
@media (min-width: 768px) {
  blockquote {
    width: 80%;
  }
}
blockquote p {
  display: inline;
  text-indent: 0;
}
blockquote p:first-child {
  margin-bottom: 1rem;
}

cite {
  font-family: var(--bs-font-sans-serif);
  display: block;
  font-style: normal;
  font-weight: 700;
  text-indent: 0;
  margin-top: 0.5rem;
}

.stretched-link,
.m2m-link {
  color: #6c6c5f !important;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  text-decoration-thickness: 2px;
  -webkit-text-decoration-color: #acac98 !important;
  text-decoration-color: #acac98 !important;
}
.stretched-link:hover,
.m2m-link:hover {
  color: #98988f;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  -webkit-text-decoration-color: #98988f;
  text-decoration-color: #98988f;
  text-decoration-thickness: 3px;
}

.m2m-link-hover:hover {
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  text-decoration-thickness: 3px;
}

.m2m-entry a:not(.btn),
.m2m-entry a:not(.m2m-link),
.m2m-entry a:not(.btn-m2m),
.m2m-link-entry {
  color: #0d6efd;
  -webkit-text-decoration-color: #0d6efd;
