.m2m-entry a:hover:not(.btn-m2m),
.m2m-link-entry:hover {
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  text-decoration-thickness: 3px;
}
.page-page-not-found a[href^="/tag/"],
.page-search a[href^="/tag/"] {
  text-transform: capitalize;
}
.page-page-not-found a[href^="/tag/"]::before,
.page-search a[href^="/tag/"]::before {
  content: "Tag Archive: ";
}
.page-page-not-found a[href^="/category/"]::before,
.page-search a[href^="/category/"]::before {
  content: "Category Archive: ";
}
.previous-next-post-links a {
